@import "main-module";

.btn {  
    text-align: center;
    font-family: Raleway;
    font-size: 13px;
    font-style: normal;
    font-weight: 700;
    line-height: 140%;
    letter-spacing: 0.52px;
    color: #fff;
    border-radius: 25px;
    display: inline-block;
    padding: 7px 28px;
    background-color: #00D5C8;
    transition: background-color .3s linear;
    
    &:hover:not(:active) {
        background-color: #029a90;
    }


    &_empty {
        background-color: transparent;
        color: #00D54B;
        border: 1px solid #00D54B;

        &:hover:not(:active) {
            background-color: #00D54B;
            color: #fff;
        }
    }

    &_green {
        background-color: #0BE05C;
        color: #fff;
        border-color: transparent;

        :hover:not(:active) {
            background-color: #009684;
        }
    }

    &_white {
        background-color: transparent;
        border: 1px solid #00D5C8;
        color: #00D5C8;

        &:hover:not(:active) {
            background-color: #00D5C8;
            color: #fff;
        }
    }
}