@import "main-module";

.filter {
    display: flex;
    padding-bottom: 26px;
    border-bottom: 1px solid #000;
    margin-bottom: 70px;

    @include below(1280px) {
        flex-wrap: wrap;
    }

    @include below(768px) {
        margin-bottom: 50px;
    }

    &__title {
        font-size: 22px;
        font-weight: 700;
        line-height: normal;
        letter-spacing: 0.44px;
        margin-right: 20px;

        @include below(768px) {
           font-size: 20px;
           margin: 0;
        }
    }

    &__date {
        width: 100%;
        max-width: 230px;

        @include below(768px) {
            max-width: initial;
            margin-top: 20px;
        }
    }

    &__date-select {
        width: 100%;
        max-width: 230px;

        @include below(768px) {
            max-width: initial;
            height: 45px;
        }

        &.select {
            & ~ .select2-container {
                .select2-selection--single {
                    border: 2px solid #00D54B;

                    @include below(768px) {
                        height: 45px;
                    }

                    .select2-selection__rendered {
                        padding: 0;
                        display: flex;
                        align-items: center;
                        color: #00D54B;
                    }
                }
            }
       }
    }  
       
    &__row {
        display: flex;
        margin-left: auto;

        @include below(1280px) {
            width: 100%;
            margin: 30px 0 0;
        }
        @include below(992px) {
            flex-wrap: wrap;
        }
    }

    &__col {
        padding: 0 20px;
        border-left: 1px solid #D8D8D8;

        @include below(1280px) {
            width: 20%;
        }

        @include below(992px) {
            width: 100%;
            padding: 10px 0;
            border-left: none;
            border-bottom: 1px solid #D8D8D8;
        }

        &_green {
            color: #00D54B;
            border: none;
            padding: 0 20px 0 0;

            @include below(992px) {
                padding: 0 0 10px;
                border-left: none;
                border-bottom: 1px solid #D8D8D8;
            }
        }

        &_light-green {
            color: #ADE74F;
        }

        &_yellow {
            color: #FED33C;
        }

        &_orange {
            color: #FE823C;
        }

        &_red {
            color: #FE3C3C;
            padding: 0 0 0 20px;

            @include below(992px) {
                padding: 10px 0 0;
                border: none;
            }
        }
    }

    &__icons {
        margin: 0 0 5px;

        svg {
            width: 25px;
            height: 25px;
        }
    }

    &__flex {
        display: flex;
        justify-content: space-between;
    }

    &__text {
        color: #B6B6B6;
        font-size: 14px;
        font-weight: 600;
        line-height: 30px;
        text-decoration-line: underline;
    }

    &__percent {
        font-size: 14px;
        font-weight: 700;
        line-height: 30px;
        margin-left: 20px;
    }
}