@import "main-module";

.header {
    position: relative;
    z-index: 100;
    // background-color: $color_black;
    padding: 32px 0;

    &.header_second {
        background-color: #000;
    }

    @include below(1024px) {
        padding: 15px 0;
    }

    &__row {
        justify-content: space-between;
        align-items: center;
        display: flex;
    }

    &__auth {
        @include below(1024px) {
            width: 100%;
            max-width: 291px;
            font-size: 18px;
            text-transform: uppercase;
            padding: 10px;
            background-color: #fff;
            color: #00D54B;
            &:hover:not(:active) {
                color: #fff;
            }
        }
    }

    &__burger {
        width: 34px;
        height: 34px;
        
        svg {
            width: 100%;
            height: 100%;
        }

        &.is-active svg {
            transform: scale(1.4);
        }

        
        @include above(1025px) {
            display: none;
        }
    }

    &__logo {
        width: 300px;
        margin-right: 100px;

        @include below(1480px) {
            width: 150px;
            min-width: 120px;
            margin-right: 20px;
        }
    }

    &__right {
        flex: auto;
        @include above(1025px) {
            display: block !important;
        }
        @include below(1024px) {
            display: none;
            position: absolute;
            top: 83.5px;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
            background-color: #00D54B;
            padding: 30px calc(var(--bs-gutter-x) * .63);
            
      
            overflow-y: auto;
            max-height: 100%;
            min-height: 100vh;
            padding-bottom: 144px;
        }

        &-top {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 27px;

            @include below(1024px) {
                margin-top: 25px;
                border-top: 1px solid #000;
                margin-bottom: 0;
                display: block;
            }    
        }
    }

    &__right-menu {
        @include below(1024px)  {
            display: flex;
            flex-direction: column-reverse;
        }
    }

    &__nav {
        @include below(1024px) {
            padding: 20px 0;
        }
        
        ul {
            display: flex;
            align-items: center;

            @include below(1024px) {
                align-items: flex-start;
                flex-direction: column;
            }
        }

        li {
            &:not(:last-child) {
                margin-right: 30px;

                @include below(1024px) {
                    margin-right: 0;
                    margin-bottom: 10px;
                }
            }
        }

        li a {
            font-size: 16px;
            font-weight: 700;
            line-height: normal;
            display: inline-block;
            color: rgba(255, 255, 255, 0.70);
            transition: color .3s linear;
            padding: 3px 0;
            &:hover:not(:active) {
                color: rgba(255, 255, 255, 1);
            }

            @include below(1024px) {
                font-size: 18px;
                color: #000;
            }
        }
    }
    
    &__menu-sub {
        position: absolute;
        width: 100%;
        top: 0;
        left: 0;
        z-index: -1;
        border-radius: 0px 40px 0px 30px;
        border: 0px solid #FFF;
        background: #333;
        display: none;
        padding: 54px 15px 30px;

        &:hover {
            a:not(:hover) {
                opacity: .5;
            }
        }

        @include below(1024px) {
            border-radius: 0px 12px;
            position: static;
            padding: 20px 10px 20px 20px;
            margin-top: 10px;
        }

        li {
            &:not(:last-child) {
                margin-bottom: 5px;
            }
        }

        li a {
            display: flex;
            align-items: center;

            color: #fff;

            font-size: 15px;
            font-weight: 700;
            line-height: 100%;
            padding: 3px 0;
            transition: opacity .3s linear;

            svg {
                transform: rotate(-90deg);
                min-width: 10px;
                min-height: 10px;
                max-width: 10px;
                max-height: 10px;
                margin-right: 12px;
            }
        }
    }

    &__menu {
        display: flex;
        align-items: center;
        position: relative;
        z-index: 1;
        @include below(1024px) {
            align-items: flex-start;
            flex-direction: column;
        }
        &-item {
            position: relative;
            @include below(1024px) {
                width: 100%;
            }

            &:not(:last-child) {
                margin-right: 10px;

                @include below(1480px) {
                    margin-right: 5px;
                }

                @include below(1024px) {
                    margin-right: 0;
                    margin-bottom: 20px;
                }
            }
        }

        &-link {
            color: #fff;
            font-size: 16px;
            font-weight: 700;
            line-height: 100%;
            text-transform: uppercase;
            display: flex;
            align-items: center;
            padding: 10px 25px 10px 32px;
            border-radius: 0px 50px;
            border: 2px solid #FFF;
            white-space: nowrap;
            transition: background-color .3s linear;
            &.is-active,
            &:hover {
                background-color: #00D54B;
            }

            @include below(1480px) {
                font-size: 12px;

                padding: 10px 25px;
            }

            @include below(1024px) {
                font-size: 20px;
                padding: 0;
                border: 0;
                border-radius: 0;
            }
         
            svg {
                min-width: 10px;
                min-height: 10px;
                max-width: 10px;
                max-height: 10px;
                margin-left: 7px;
            }
        }
    }

    &__profile {
        display: flex;
        align-items: center;

        @include below(1024px) {
        }

        @include below(420px) {
            justify-content: space-between;
        }
    }

    &__user {
        color: #FFF;
        font-size: 14px;
        font-weight: 500;
        line-height: 140%;
        letter-spacing: 0.98px;
        text-decoration-line: underline;
        display: inline-block;
        padding: 3px 0;
        margin-right: 25px; 

        transition: opacity .3s linear;

        &:hover:not(:active) {
            opacity: 0.7;
        }

        @include below(1024px) {
            font-size: 16px;
            margin-right: 20%;
        }
    
        @include below(420px) {
            margin-right: 0;
        }
    }

    &__logout {
        color: #00D5C8;
        font-size: 13px;
        font-weight: 700;
        line-height: 140%;
        letter-spacing: 0.52px;
        padding: 3px 0;
        transition: opacity .3s linear;

        &:hover:not(:active) {
            opacity: 0.7;
        }

        @include below(1024px) {
            color: #fff;
            font-size: 16px;
        }
    }
}