@import "main-module";

.rates {
    padding: 97px 0 0;

    @include below(800px) {
        padding: 64px 0 100px;
    }

    &_second {
        padding: 65px 0 110px;

        @include below(768px) {
            padding: 50px 0 70px;
        }
    }

    &__title {
        text-align: center;
        font-size: 50px;
        font-weight: 700;
        line-height: 120%;
        margin-bottom: 60px;

        @include below(1024px) {
            font-size: 36px;
            margin-bottom: 40px;
        }

        span {
            color: #00D54B;
        }
    }

    &__row {
        display: flex;
        align-items: stretch;
        flex-wrap: wrap;
        margin: 0 -8.5px;

        
        @include below(1340px) {
            margin: -15px -8.5px 0;
        }

        @include below(768px) {
            margin: -15px 0 0;
        }
    }

    &__item {
        display: flex;
        flex-direction: column;
        width: calc(25% - 17px);
        margin: 0 8.5px;
        padding: 7px;
        border-radius: 0px 50px;
        border: 1px solid #D8D8D8;
        background-color: #FFF;

        @include below(1340px) {
            width: calc(33.333% - 17px);
            margin: 15px 8.5px 0;
        }

        @include below(1024px) {
            width: calc(50% - 17px);
            margin: 15px 8.5px 0;
        }

        @include below(768px) {
            width: 100%;
            margin: 15px 0 0;
        }
    }

    &__top {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &__img {
        width: 100%;
        max-width: 280px;

        @include below(768px) {
            max-width: 250px;
        }
    }

    &__body {
        margin-top: auto;
        border-radius: 25px 25px 0px 44px;

        @include below(768px) {
            border-radius: 44px 44px 0px 44px;
        }

        &_first {
            background-color: #CCF7DB;

            .rates__price {
                color: #009635;
            }

            .rates__body-bottom {
                color: #00D54B;
            }
        }

        &_second {
            background-color: #CCF7E8;

            .rates__price {
                color: #009684;
            }

            .rates__body-bottom {
                color: #00D5A2;
            }
        }

        &_third {
            background-color: #CCF7F4;

            .rates__price {
                color: #189FB1;
            }

            .rates__body-bottom {
                color: #00D5D5;
            }
        }

        &_fourth {
            background-color: #CCEFF7;

            .rates__price {
                color: #1782AF;
            }

            .rates__body-bottom {
                color: #00AFD5;
            }
        }
    }

    &__body-title {
        padding: 28px 0 0 20px;
        font-size: 24px;
        font-weight: 700;
        line-height: 130%; 
    }

    &__price {
        padding-left: 20px;
        &-text {
            font-size: 18px;
            font-weight: 700;
            line-height: 100%;

            @include below(768px) {
                font-size: 16px;
            }
        }

        strong {
            font-size: 30px;
            font-weight: 700;
            line-height: normal;
            text-transform: uppercase;
            margin-left: 5px;

            @include below(768px) {
                font-size: 29px;
            }
        }
    }

    &__body-text {
        font-size: 14px;
        font-weight: 500;
        line-height: normal;
        text-transform: uppercase;

        @include below(768px) {
            font-size: 13px;
        }
    }

    &__body-bottom {
        margin-top: 25px;
        display: flex;
        align-items: end;
        justify-content: space-between;
        padding: 0 23px 23px 20px;

        @include below(768px) {
            margin-top: 10px;
            padding: 0 15px 20px 20px;
        }

        strong {
            font-size: 40px;
            font-weight: 700;
            line-height: normal;

            @include below(1024px) {
                font-size: 35px;
            }

            @include below(768px) {
                font-size: 20px;
            }
        }
    }

    &__body-btn {
        width: 50px;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: linear-gradient(180deg, #00D54B 0%, #00D5C8 100%);
        border-radius: 50%;

        @include below(1024px) {
            width: 42px;
            height: 42px;
        }

        svg {
            width: 16px;
            height: 16px;

            @include below(1024px) {
                width: 14px;
                height: 14px;
            }
        }
    }

    &__btn {
        display: block;
        width: 100%;
        max-width: 270px;
        margin: 62px auto 0;
        padding: 17px 0 19px;
        font-size: 18px;
        text-transform: uppercase;
        border-radius: 100px;
        @include below(768px) {
            margin: 50px auto 0;
        }

        @include below(479px) {
            max-width: initial;
        }
    }
}