@import "main-module";

.table {

    &__row {
        position: relative;
        display: flex;
        align-items: center;
        padding: 20px 0;
        border-bottom: 1px solid #D8D8D8;

        @include below(1080px) {
            flex-direction: column;
            align-items: start;
            padding: 30px;
            border-radius: 0px 40px;
            border: 1px solid #E7E7E7;
            background: #FFF;
            box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.25);

            &:not(:last-child) {
                margin-bottom: 15px;
            }
        }

        
        @include below(768px) {
            padding: 20px 20px 20px 60px;
        }
    }

    &__col {
        display: flex;
        align-items: center;

        &:nth-of-type(2) {
            flex: auto;
            margin-left: 65px;

            @include below(1240px) {
                margin-left: 30px;
            }

            @include below(1080px) {
                margin: 30px 0;
                align-items: stretch;
            }

            @include below(768px) {
                margin: 5px 0 15px;
                flex-direction: column;
                align-items: start;
            }
        }

        

        &:nth-of-type(3) {
            justify-content: end;
            width: 17%;
            margin-left: 20px;


            @include below(1340px) {
                width: 20%;
            }

            @include below(1080px) {
                width: initial;
                margin: 0;
            }
        }

        &-text {
            text-align: center;
            color: #00D5C8;
            font-size: 16px;
            font-weight: 700;
            line-height: 120%;
            letter-spacing: 0.32px;

            @include below(1080px) {
                text-align: start;
            }

            @include below(768px) {
                font-size: 14px;
            }
        }

        &-numb {
            font-size: 20px;
            font-weight: 700;
            line-height: 24px;
            margin-right: auto;

            @include below(1080px) {
                margin-right: 20px;
            }

            @include below(768px) {
               font-size: 16px;
            }
        }
    }

    &__sport {
        display: flex;
        align-items: center;
        color: #CCC;
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: 100%;
        text-transform: uppercase;
        margin-right: 65px;

        @include below(1240px) {
            margin-right: 30px;
        }

        @include below(768px) {
            margin: 0;
            position: absolute;
            top: 20px;
            right: 15px;
            font-size: 12px;
        }

        @include below(479px) {
            font-size: 9px;
        }

        svg {
            width: 27px;
            height: 27px;
            margin-right: 17px;

            
            @include below(768px) {
                width: 13px;
                height: 13px;
                margin-right: 5px;
            }
        }
    }

    &__date {
        strong {
            font-size: 18px;
            font-weight: 700;
            line-height: normal;
            background-color: rgba(11, 224, 92, .3);
            border-radius: 48px;
            padding: 3px 15px;
            margin-right: 9px;

            @include below(768px) {
                font-size: 16px;
                position: absolute;
                top: 65px;
                left: -25px;
                margin: 0;
                transform: rotate(-90deg);
            }
        }

        span {
            color: #00D54B;
            font-size: 18px;
            font-weight: 700;
            line-height: 24px; 

            @include below(768px) {
                font-size: 14px;
            }
        }

    }

    &__match {
        display: flex;
        flex-direction: column;
        margin-right: auto;
        strong {
            font-size: 18px;
            font-weight: 700;
            line-height: 100%;
            margin-bottom: 5px;

            @include below(768px) {
                font-size: 15px;
            }
        }

        span {
            color: #979797;
            font-size: 12px;
            font-weight: 700;
            line-height: 100%;
            opacity: 0.7;
        }

        @include below(1340px) {
            max-width: 164px;
        }

        @include below(1080px) {
            max-width: initial;
        }
    }

    &__match-score {
        text-align: center;
        width: 47%;
        font-size: 20px;
        font-weight: 700;
        line-height: 24px;

        @include below(1080px) {
            width: initial;
            margin-left: 40px;
            text-align: left;
        }

        @include below(768px) {
            font-size: 14px;
            margin: 10px 0 0;

        }

        span {
            color: #00D54B;
        }

        &_second {
            span {
                color: #00D5C8;
            }
        }
    }

    &__price {
        color: #FFF;
        text-align: center;
        font-size: 16px;
        font-weight: 700;
        line-height: normal;
        letter-spacing: 0.32px;
        border-radius: 100px;
        background-color: #00D5C8;
        padding: 7px 23px;
        margin-right: 20px;

        @include below(768px) {
            margin-right: 10px;
        }
    }

    &__link {
        text-decoration: underline;
        font-size: 16px;
        font-weight: 700;
        line-height: normal;
        letter-spacing: 0.32px;

        &_red {
            color: #FE3C3C
        }

        &_green {
            color: #00D54B;
        }

        &_grey {
            color: #CCC;
        }
    }

    &__btn {
        display: block;
        width: 100%;
        max-width: 345px;
        margin: 60px auto 0;
        padding: 17px 0 19px;
        border-radius: 100px;
        font-size: 18px;
        font-weight: 700;
        line-height: 140%;
        letter-spacing: 0.72px;
        text-transform: uppercase;

        &_second {
            max-width: 275px;
        }

        @include below(479px) {
            color: #fff;
            max-width: initial;
            margin: 45px 0 0;
            background: linear-gradient(180deg, #00D54B 0%, #00D5C8 100%);
        }
    }

}