@import "main-module";

.pagination {

    &__list  {
        display: flex;
        align-items: center;
        margin: 0 -6pc;
    }

    &__item {
        margin: 0 6px;
    }

    &__link {
        width: 30px;
        height: 30px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        font-size: 16px;
        font-weight: 700;
        line-height: normal;
        letter-spacing: 0.32px;
        color: #000;
        transition: background-color .3s linear;

        &:hover {
            background-color: #B6F6CE;
        }

        &_active {
            background-color: #B6F6CE;
        }

        svg {
            width: 6px;
            height: 13px;
        }
    }
}