@import "main-module";

.blog {

    &__item {
        padding: 85px 0;
        display: flex;
        border-bottom: 1px solid #000;

        &:first-child {
            padding: 20px 0 85px;

            @include below(992px) {
               padding: 20px 0 40px;
            }
        }

        @include below(992px) {
            flex-direction: column;
            padding: 40px 0;
        }
    }

    &__img {
        flex-shrink: 0;
        width: 100%;
        max-width: 565px;
        margin-right: 45px;

        @include below(1240px) {
            max-width: 450px;
        } 

        @include below(992px) {
            max-width: initial;
            margin: 0 0 30px;
        }

        @include below(540px) {
            margin: 0 0 20px;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 0px 50px;

            @include below(540px) {
                border-radius: 0 25px;
            }
        }
    }

    &__data {
        display: inline-block;
        color: #B6F6CE;
        font-size: 12px;
        font-weight: 700;
        line-height: 30px; 
        margin-bottom: 15px;
    }

    &__title {
        font-size: 27px;
        font-weight: 700;
        line-height: 105%;
        margin-bottom: 15px;

        @include below(768px) {
            font-size: 24px;
        }
    }

    &__text {
        font-size: 16px;
        font-weight: 500;
        line-height: 120%;
        margin-bottom: 30px;

        p {
            &:first-of-type {
                margin-bottom: 20px;
            }
        }
    }

    &__link {
        color: #00D54B;
        font-size: 14px;
        font-weight: 700;
        line-height: normal;
        letter-spacing: 0.28px;
        text-decoration-line: underline;
    }

    &__pagination {
        margin-top: 80px;
        display: flex;
        align-items: center;
        justify-content: center;

        @include below(768px) {
            margin-top: 50px;
        }
    }
}