@import "main-module";

.forecast-block {
    position: relative;
    padding: 190px 0;
    margin-bottom: 97px;

    &.forecast-block_second {
        margin-bottom: 0;

        @include below(800px) {
           margin: 50px 0 0;
        }
    }

    @include below(1700px) {
        margin-bottom: 0;
    }

    @include below(800px) {
        padding: 66px 0 68px;
        margin-bottom: 60px;
    }

    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        z-index: 0;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__wrap {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        z-index: 10;
    }

    &__inner {
        position: relative;

        @include below(800px) {
            padding: 100px 0;
        }
    }

    &__title {
        color: #fff;
        text-align: center;
        font-size: 50px;
        font-weight: 700;
        line-height: 110%; 
        margin-bottom: 60px;
        span {
            color: #00D54B;
        }

        @include below(1700px) {
           font-size: 40px;
        }

        @include below(1240px) {
            font-size: 36px;
        }

        @include below_and_above(800px, 541px) {
            margin-bottom: 80px;
        }
    }

    &__buttons {
        margin-top: 100px;
        display: flex;

        @include below_and_above(800px, 541px) {
            margin-top: 140px;
        }

        @include below(600px) {
            flex-wrap: wrap;
        }
    }

    &__btn {
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: 140%; 
        letter-spacing: 0.72px;
        text-transform: uppercase;
        padding: 17px 68px 19px;
        border-radius: 100px;

        @include below(992px) {
            padding: 17px 20px 19px;
        }

        @include below(600px) {
            width: 100%;
        }

        &:first-of-type {
            margin-right: 16px;

            @include below(600px) {
                margin: 0 0 18px;
            }
        } 
    }

    &__item {
        position: relative;
        &_first {
            transform: translateY(8px);

            @include below(1700px) {
                transform: translateY(20px);
            }

            @include below(800px) {
                transform: translateY(0);
            }

            @include below(540px) {
                transform: translateY(15px);
            }

            @include below(350px) {
                transform: translate(-2px, -2px);
            }
            .forecast-block__name {
                font-size: 50px;

                @include below(1240px) {
                    font-size: 42px;
                }
            }

            .forecast-block__img {
                max-width: 410px;

                @include below(1540px) {
                    max-width: 380px;
                }

                @include below(1470px) {
                    max-width: 300px;
                }

                @include below(992px) {
                    max-width: 250px;
                }

                @include below(540px) {
                    max-width: 198px;
                }
            }
        }

        &_second {
            position: absolute;
            top: -40px;
            right: -420px;

            @include below(1470px) {
                top: -110px;
                right: -380px;
            }

            @include below(1200px) {
                top: -75px;
                right: -270px;
            }

            @include below(1050px) {
                right: -210px;
            }

            @include below(992px) {
                right: -150px;
            }

            @include below(800px) {
                right: 40px;
                top: 360px;
            }

            @include below(540px) {
                right: 40px;
                top: 340px;
            }

            .forecast-block__img {
                max-width: 248px;

                @include below(1240px) {
                    max-width: 200px;
                }

                @include below(992px) {
                    max-width: 180px;
                }

                @include below(540px) {
                    max-width: 120px;
                }
            }

            .forecast-block__name {
                font-size: 35px;

                @include below(1540px) {
                    font-size: 25px;
                }

                @include below(1140px) {
                    font-size: 19px;
                }

                @include below(992px) {
                   font-size: 19px;
                }
            }
        }

        &_third {
            position: absolute;
            top: -20px;
            left: -350px;

            @include below(1470px) {
                top: -100px;
                left: -310px;
            }

            @include below(1200px) {
                top: -60px;
                left: -225px;
            }

            @include below(1050px) {
                left: -200px;
            }

            @include below(992px) {
                left: -120px;
            }

            @include below(800px) {
                left: 45px;
            }

            @include below(540px) {
                top: -25px;
                left: 48px;
            }

            .forecast-block__img {
                max-width: 215px;

                @include below(1240px) {
                    max-width: 180px;
                }

                @include below(992px) {
                    max-width: 150px;
                }

                @include below(540px) {
                    max-width: 103px;
                }
            }

            .forecast-block__name {
                font-size: 35px;

                @include below(1540px) {
                    font-size: 25px;
                }

                @include below(1140px) {
                    font-size: 21px;
                }
            }
        }

        &_fourth {
            position: absolute;
            bottom: -20px;
            right: -220px;

            @include below(1050px) {
                right: -170px;
            }

            @include below(800px) {
                bottom: 25px;
                right: 0;
                transform: translateX(-220px);
            }

            @include below(540px) {
                bottom: 10px;
                transform: translateX(-160px);
            }

            .forecast-block__img {
                max-width: 181px;

                @include below(1240px) {
                    max-width: 160px;
                }

                @include below(992px) {
                    max-width: 140px;
                }

                @include below(540px) {
                    max-width: 88px;
                }
            }

            .forecast-block__name {
                font-size: 23px;

                @include below(1340px) {
                    font-size: 18px;
                }
                @include below(540px) {
                    font-size: 14px;
                }
            }
        }

        &_fifth {
            position: absolute;
            bottom: 0;
            left: -185px;
            @include below(992px) {
                left: -150px;     
            }

            @include below(800px) {
                bottom: 310px;
                left: 0;
                transform: translateX(200px);
            }

            @include below(540px) {
                bottom: 275px;
                transform: translateX(160px);
            }

            .forecast-block__img {
                max-width: 170px;

                @include below(1240px) {
                    max-width: 140px;
                }

                @include below(992px) {
                    max-width: 130px;
                }

                @include below(540px) {
                    max-width: 82px;
                }

            }

            .forecast-block__name {
                font-size: 28px;

                @include below(1340px) {
                    font-size: 22px;
                }

                @include below(1240px) {
                    font-size: 17px;
                }
            }
        }

        &_sixth {
            position: absolute;
            bottom: 70px;
            right: -530px;

            @include below(1470px) {
                bottom: 30px;
                right: -440px;
            }

            @include below(1200px) {
                bottom: 55px;
                right: -360px;
            }

            @include below(1050px) {
                right: -300px;
            }

            @include below(992px) {
                right: -260px;
                bottom: 100px;
            }

            @include below(800px) {
                right: -80px;
                bottom: 50px;
            }

            @include below(540px) {
                right: -45px;
                bottom: 25px;
            }

            .forecast-block__img {
                max-width: 150px;

                @include below(1240px) {
                    max-width: 120px;
                }

                @include below(992px) {
                    max-width: 110px;
                }

                @include below(540px) {
                    max-width: 72px;
                }
            }

            .forecast-block__name {
                font-size: 22px;

                
                @include below(1240px) {
                    font-size: 16px;
                }

                @include below(540px) {
                    font-size: 13px;
                }
            }
        }

        &_seventh {
            position: absolute;
            bottom: 80px;
            left: -500px;

            @include below(1470px) {
                bottom: 50px;
                left: -375px;
            }

            @include below(1200px) {
                bottom: 80px;
                left: -300px;
            }

            @include below(992px) {
                bottom: 90px;
                left: -235px;
            }

            @include below(800px) {
                left: -55px;
                bottom: 310px;
            }

            @include below(540px) {
                left: -35px;
                bottom: 280px;
            }

            .forecast-block__img {
                max-width: 147px;

                @include below(1240px) {
                    max-width: 100px;
                }

                @include below(540px) {
                    max-width: 68px;
                }
            }

            .forecast-block__name {
                font-size: 26px;

                @include below(1340px) {
                    font-size: 22px;
                }

                @include below(1240px) {
                    font-size: 18px;
                }

                @include below(540px) {
                    font-size: 14px;
                }
            }
        }
    }

    &__img {
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }


    &__name {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        color: #fff;
        text-align: center;
        text-shadow: 0px 0px 15px rgba(0, 0, 0, 0.50);
        font-weight: 700;
        line-height: 100%;
        text-transform: uppercase;
    }
}