@import "main-module";

.grade {
    background-color: #EDFFFC;
    margin-top: 100px;
    padding: 80px 0 90px;

    @include below(768px) {
        margin-top: 50px;
        padding: 50px 40px 70px;
    }

    @include below(540px) {
        margin-top: 50px;
        padding: 50px 0 70px;
    }

    &__form {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__form-title {
        text-align: center;
        font-size: 36px;
        font-weight: 700;
        line-height: 100%; 
        text-transform: uppercase;
        margin-bottom: 55px;

        @include below(992px) {
           font-size: 25px;
           margin-bottom: 30px;
        }
    }

    &__form-flex {
        display: flex;

        @include below(768px) {
            flex-wrap: wrap;
        }
    }

    &__form-col {
        width: 100%;
        max-width: 557px;

        @include below(768px) {
           max-width: initial;
        }

        &:first-of-type {
            margin-right: 24px;

            @include below(768px) {
                margin: 0 0 18px;
            }
        }
    }

    &__form-label {
        display: inline-block;
        width: 100%;

        &:nth-of-type(2) {
            margin-top: 18px;
        }
    }

    &__form-input {
        height: 78px;

        @include below(768px) {
            height: 70px;
            border-radius: 20px;
        }
    }

    &__form-textarea {
        min-height: 175px;

        @include below(768px) {
            border-radius: 20px;
        }
    }

    &__form-btn {
        margin: 55px 0 0;
        width: 100%;
        max-width: 275px;
        padding: 17px 0 19px;
        border-radius: 100px;
        background: linear-gradient(180deg, #00D54B 0%, #00D5C8 100%);
        text-transform: uppercase;
        font-size: 18px;
        font-weight: 700;
        line-height: 140%; 
        letter-spacing: 0.72px;

        @include below(479px) {
            margin: 30px 0 0;
            max-width: initial;
        }
    }

    &__flex {
        margin-top: 30px;
        display: flex;
        align-items: center;

        span {
            color: #B6B6B6;
            font-size: 20px;
            font-weight: 600;
            line-height: 30px;
            margin-right: 26px;
        }
    }

    &__buttons {
        margin: 0 -3px;
        button {
            color: #B6B6B6;
            margin: 0 3px;
            svg {
                width: 25px;
                height: 25px;
            }
        }
    }
}