@import "main-module";

.forecasts {
    &_second {
        padding-bottom: 80px;

        @include below(768px) {
            padding-bottom: 60px;
        }
    }

    &__top {
        position: relative;
        margin-bottom: 80px;

        @include below(992px) {
           margin-bottom: 45px;
        }
    }

    &__img {
        height: 335px;
        @include below(1240px) {
            height: initial;
        }

        img {
            width: 100%;
            height: 100%;
            border-radius: 0 50px;
        }
    }

    &__content {
        position: absolute;
        top: 75px;
        left: 75px;
        z-index: 10;

        @include below(1240px) {
            top: 45px;
            left: 40px;
        }

        @include below(768px) {
            top: 45px;
            left: 20px;
        }
    }

    &__content-title {
        color: #fff;
        font-size: 50px;
        font-weight: 700;
        text-shadow: 0px 0px 15px rgba(0, 0, 0, 0.25);
        line-height: 100%;
        margin-bottom: 10px;

        @include below(1240px) {
            font-size: 40px;
        }

        @include below(1024px) {
            font-size: 36px;
        }
    }

    &__content-data {
        color: #00D54B;
        text-shadow: 0px 0px 15px rgba(0, 0, 0, 0.25);
        font-size: 96px;
        font-weight: 700;
        line-height: 100%;

        @include below(1340px) {
            font-size: 60px;
        }

        @include below(1240px) {
            font-size: 48px;
        }
    }

    &__flex {
        display: flex;
        padding-bottom: 30px;
        border-bottom: 1px solid #000;
        margin-bottom: 50px;

        &_second {
            margin-top: 75px;

            @include below(768px) {
                margin-top: 45px;
            }
        }

        @include below(1240px) {
            flex-direction: column;
        }

        @include below(992px) {
            padding: 0;
            border: none;
            margin-bottom: 40px;
        }

    }

    &__col {
        display: flex;

        &:not(:last-child) {
            margin-right: 70px;
        
            @include below(1240px) {
                margin: 0 0 40px;
            }
        }

  
        @include below(992px) {
            flex-direction: column;
        }

        &-title {
            white-space: nowrap;
            font-size: 22px;
            font-weight: 700;
            line-height: normal;
            letter-spacing: 0.44px;
            margin-right: 25px;

            @include below(992px) {
                margin: 0 0 20px;
            }
        }
    }

    &__buttons {
        display: flex;
        align-items: start;

        &-btn {
            &:first-of-type {
                margin-right: 10px;
            }
        }
    }

    &__list {
        display: flex;
        align-items: stretch;
        flex-wrap: wrap;
        margin: -9px -2px 0;
    }

    &__list-item {
        margin: 9px 2px 0;
    }

    &__list-link {
        display: flex;
        align-items: center;
        padding: 5px 6px;
        border-radius: 100px;
        border: 1px solid #CCC;
        color: #CCC;
        font-size: 9px;
        font-style: normal;
        font-weight: 700;
        line-height: 100%;
        text-transform: uppercase;

        svg {
            width: 13px;
            height: 13px;
            margin-right: 5px;
        }

        &_last {
            padding: 8px 10px;
            border: none;
            color: #fff;
            background-color: #00D54B;
        }
    }

    &__head {
        margin: 70px 0 75px;
        padding-bottom: 25px;
        border-bottom: 1px solid #000;

        @include below(768px) {
            margin: 40px 0;
        }

        &-title {
            color: #00D54B;
            font-size: 36px;
            font-weight: 700;
            line-height: 100%; 
            text-transform: uppercase;
            margin-bottom: 45px;

            @include below(1240px) {
                font-size: 30px;
                margin-bottom: 20px;
            }

            @include below(768px) {
                font-size: 24px;
            }
        }

        &-flex {
            display: flex;

            @include below(1024px) {
                flex-direction: column;
            }
        }

        &-row {
            display: flex;
            margin: 0 0 40px;

            @include below(1024px) {
                flex-direction: column;
            }
        }
    }

    &__item {
        display: flex;
        align-items: center;

        &:not(:first-child){
            margin-left: 55px;

            @include below(1024px) {
                margin: 20px 0 0;
            }
        }

        &_first {
            width: 16%;
            @include below(1024px) {
                width: 100%;
            }
        }

        &_second {
            width: 27%;
            @include below(1024px) {
                width: 100%;
            }
        }

        @include below(768px) {
            flex-direction: column;
            align-items: start;
        }

        &-links {
            display: flex;
            flex-wrap: wrap;
            margin: -5px -4px 0;
        }

        &-link {
            padding: 8px 25px;
            border-radius: 100px;
            border: 2px solid #B7B7B7;
            color: #00D5C8;
            text-align: center;
            font-size: 14px;
            font-weight: 700;
            line-height: normal;
            letter-spacing: 0.28px;
            transition: border-color .3s linear;
            margin: 5px 4px 0;

            &:hover {
                border-color: #00D5C8;
            }
        }

        &-title {
            white-space: nowrap;
            font-size: 22px;
            font-weight: 700;
            line-height: normal;
            letter-spacing: 0.44px;
            margin-right: 20px;

            @include below(768px) {
                margin: 0 0 20px;
            }
        }
    }

    &__head-subtitle {
        white-space: nowrap;
        font-size: 22px;
        font-weight: 700;
        line-height: normal;
        letter-spacing: 0.44px;
        margin-right: 20px;

        @include below(1024px) {
            margin: 0 0 20px;
        }
    }

    &__year {
        width: 100%;
        max-width: 167px;

        @include below(479px) {
            max-width: 100%;
            height: 40px;
        }
    }

    &__item-select {
        width: 100%;
        max-width: 231px;

        @include below(479px) {
            max-width: 100%;
            height: 40px;
        }
    }

    &__select {
        width: 100%;

        &.select {
            & ~ .select2-container {
                .select2-selection--single {
                    @include below(768px) {
                        height: 40px;
                    }
                }
            }
        }
    }

}