@import "main-module";

.answer {

    &__top {
        padding-bottom: 40px;
        border-bottom: 1px solid #B7B7B7;
    }

    &__list {
        display: flex;
        align-items: stretch;
        flex-wrap: wrap;
        margin: -12px -4.5px 0;

        @include below(479px) {
            margin: -10px 0 0;
        }
    }

    &__list-item {
        margin: 12px 4.5px 0;

        @include below(479px) {
            width: 100%;
            margin: 10px 0 0;
        }
    }

    &__list-link {
        display: inline-block;
        color: #00D54B;
        text-align: center;
        font-size: 14px;
        font-weight: 700;
        line-height: normal;
        letter-spacing: 0.28px;
        padding: 8px 13px;
        border: 1px solid #00D54B;
        border-radius: 100px;
        transition: background-color .3s linear, color .3s linear;

        @include below(479px) {
            display: block;
        }

        &:hover {
            background-color: #00D54B;
            color: #fff;
        }

        &_active {
            background-color: #00D54B;
            color: #fff;
        }
    }

    
    &__top-btn {
        margin-top: 36px;
        font-size: 18px;
        font-weight: 700;
        line-height: 140%;
        letter-spacing: 0.72px;
        text-transform: uppercase;
        padding: 17px 57px 19px;
        border-radius: 100px;

        @include below(479px) {
            width: 100%;
            padding: 17px 0 19px;
        }
    }

    &__pagination {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 76px 0 102px;

        @include below(768px) {
            margin: 50px 0 60px;
        }
    }

    &__item {
        padding: 70px 0 60px;
        border-bottom: 1px solid #B7B7B7;

        @include below(768px) {
            padding: 40px 0 30px;
        }

        h2 {
            font-size: 24px;
            font-weight: 700;
            line-height: 130%;
            margin-bottom: 30px;

            @include below(768px) {
                font-size: 20px;
                margin-bottom: 20px;
            }
        }
    }

    &__item-block {
        padding: 50px;
        background-color: #EDFFFC;
        display: flex;

        @include below(992px) {
            padding: 40px 30px;
            flex-direction: column;
        }
    }

    &__item-icon {
        
        svg {
            width: 39px;
            height: 39px;
            margin-right: 30px;

            @include below(992px) {
                width: 30px;
                height: 30px;
                margin: 0 0 20px;
            }
        }
    }

    &__item-right {
        p {
            font-size: 18px;
            font-weight: 600;
            line-height: 140%;
            margin: 0 0 10px;

            @include below(768px) {
               font-size: 16px;
               line-height: 120%;
            }

            a {
                font-weight: 700;
                color: #00D54B;
                text-decoration: underline;
                br {
                    display: none;

                    @include below(992px) {
                        display: block;
                    }
                }

                @include below(768px) {
                    font-size: 13px;
                }
            }
        }

        hr {
            background-color: #D8D8D8;
            margin: 30px 0 60px;

            @include below(768px) {
                margin: 30px 0;
            }
        }
    }


    &__item-link {
        padding: 11px 25px;
        margin-top: 40px;
        font-size: 16px;
        font-weight: 700;
        line-height: 140%; 
        letter-spacing: 0.64px;
        text-transform: uppercase;
        border-radius: 100px;

        @include below(768px) {
            margin-top:  30px;
        }
        @include below(479px) {
           width: 100%;
        }
    }

    &__bottom {
        background-color: #EDFFFC;
        padding: 77px 0 93px;

        
        @include below(768px) {
            padding: 40px 20px;
        }
    }

    &__form {
        width: 100%;
        max-width: 427px;
        margin: 0 auto;

        @include below(768px) {
            max-width: initial;
            margin: 0;
        }
    }

    &__form-title {
        font-size: 36px;
        font-style: normal;
        font-weight: 700;
        line-height: 100%;
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 35px;

        @include below(768px) {
            font-size: 25px;
            margin-bottom: 25px;
        }
    }

    &__form-label {
        width: 100%;
        display: block;

        &:not(:last-child) {
            margin-bottom: 18px;

            @include below(768px) {
                margin-bottom: 10px;
            }
        }
    }

    &__form-input{
       width: 100%;
       height: 78px;

        @include below(768px) {
            border-radius: 20px;
            height: 60px;
        }
    }

    &__form-checkbox {
        margin-top: 45px;

        @include below(479px) {
            margin-top: 30px;
        }
    }

    &__form-btn {
        display: block;
        margin: 40px auto 0;
        padding: 17px 0 19px;
        width: 100%;
        max-width: 275px;
        font-size: 18px;
        font-weight: 700;
        line-height: 140%;
        letter-spacing: 0.72px;
        text-transform: uppercase;
        border-radius: 100px;
        background: linear-gradient(180deg, #00D54B 0%, #00D5C8 100%);

        @include below(479px) {
            margin: 30px 0 0;
            max-width: initial;
        }
    }
}