@import "main-module";

.checkbox {
    &__wrap {
        display: inline-flex;
        cursor: pointer;
        input {
            position: absolute;
            top: 0;
            left: -1000%;

            &:checked ~ .checkbox__mark {
                background-color: #00D54B;
                border-color: #00D54B;
                color: #fff;
            }

            &:disabled {
                & ~ .checkbox__mark {
                    background-color: #F7F9FB;
                    border-color: #F7F9FB;
                }

                & ~ .checkbox__text {
                    color: #808191;
                }
            }
        }
    }

    &__mark {
        width: 25px;
        height: 25px;
        flex-shrink: 0;
        border: 2px solid #CCEFF7;
        background-color: #FFF;
        display: block;
        color: transparent;
        transition: background-color .3s linear, color .3s linear, border-color .3s linear;
        svg {
            width: 22px;
            height: 22px;
        }
    }

    &__text {
        font-family: Raleway;
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: 120%;
        margin-left: 16px;

        a {
            color: #00D54B;
            text-decoration: underline;
            transition: text-decoration .3s liner;

            &:hover {
                text-decoration: none;
            }
        }
    }
}