@import "main-module";

.statistics {
    table {
        width: 100%;
        tbody {
            tr {                  
                td {
                    padding: 22px 0 28px;
                    text-align: center;
                    font-family: Raleway;
                    font-size: 20px;
                    font-style: normal;
                    font-weight: 700;
                    line-height: 24px; 
                    border-bottom: 1px solid #D8D8D8;
    
                    @include below(1240px) {
                        padding: 15px;
                        font-size: 18px;
                    }
    
                    @include below(768px) {
                        font-size: 16px;
                    }
    
                    &:first-of-type {
                        text-align: start;
                        color: #CCC;
                        font-size: 15px;
                        font-weight: 700;
                        line-height: 100%; 
                        text-transform: uppercase;
    
                        @include below(1240px) {
                            padding: 15px 0;
                        }
    
                        @include below(768px) {
                            font-size: 14px;
                        }
                    }
                }    
            }
        }
    }
    &_first{
        @include below(1240px) {
            max-width: 1240px;
            overflow-x: auto;
            overflow-y: hidden;
        }
        table {
            thead {
                th {
                    padding: 15px 0;
                    color: #00D5C8;
                    font-size: 15px;
                    font-weight: 700;
                    line-height: 100%; 
                    text-transform: uppercase;
                    border-bottom: 1px solid #00D54B;
    
                    @include below(1240px) {
                        padding: 15px 10px;
                    }
    
                    @include below(768px) {
                        font-size: 14px;
                    }
    
                    &:first-of-type {
                        color: #000;
                        text-align: start;
                        @include below(1240px) {
                            padding: 15px 0;
                        }
                    }
                }
            }
            tbody {
                tr {
                    &:nth-of-type(5) {
                        td {
                            &:not(:first-child) {
                                font-size: 23px;
    
                                @include below(1240px) {
                                    font-size: 18px;
                                }
    
                                @include below(768px) {
                                    font-size: 16px;
                                }
                            }
    
                            span {
                                color: #00D54B;
                            }
                        }
                    }
    
                    &:nth-of-type(6) {
                        td {
                            span {
                                color: #00D5C8;
                            }
                        }
                    }
                }
            }
        }
    }
    &_second {
        @include below(550px) {
            max-width: 600px;
            overflow-x: auto;
            overflow-y: hidden;
        }
        table {
            width: 100%;
            thead {
                th {
                    width: 33.333%;
                    padding: 15px 0;
                    color: #00D54B;
                    font-size: 15px;
                    font-weight: 700;
                    line-height: 100%; 
                    text-transform: uppercase;
                    border-bottom: 1px solid #00D54B;
    
                    &:first-of-type {
                        text-align: start;
                    }
    
                    br {
                        display: none;
                        @include below(550px) {
                            display: block;
                        }
                    }
    
                    @include below(768px) {
                        font-size: 14px;
                    }
                }
            }
            tbody {
                tr {
                    td {
                        width: 33.333%;
                    }
                }
            }
        }
    }
}