@import "main-module";

.reviews  {
    padding: 82px 0 101px;

    @include below(768px) {
        padding: 86px 0 95px;
    }

    &_second {
        padding: 80px 0 0;

        @include below(768px) {
            padding: 50px 0 0;
        }
    }

    &__title {
        text-align: center;
        font-size: 50px;
        font-style: normal;
        font-weight: 700;
        line-height: 100%;
        margin-bottom: 60px;

        @include below(1024px) {
            font-size: 36px;
            margin-bottom: 48px;
        }
    }

    &__wrap {
        position: relative;
    }

    &__row {
        display: flex;
        max-width: 1193px;

        &.swiper {
            margin: -60px auto;
            padding: 60px 30px;
 
            @include below(1240px) {
                padding: 60px 50px;
            }

            @include below(768px) {
                margin: -48px 0;
                padding: 48px 10px;
            }
        }

        .swiper-wrapper {
            display: flex;
            align-items: stretch;
        }

        .swiper-slide:not(.swiper-slide-active):not(.swiper-slide-next) {
            opacity: 0;
            transition: opacity .3s linear;
        }
    }

    &__nav {
        .swiper-button-prev {
            width: 40px;
            height: 40px;
            left: 0;
            &::after {
                content: "";
                width: 40px;
                height: 40px;
                display: inline-block;
                background-image: url("../img/sprites/arrow_green_left.svg");
                background-position: center;
                background-repeat: no-repeat;
            }

            @include below(768px) {
                display: none;
            }
        }
        .swiper-button-next {
            width: 40px;
            height: 40px;
            right: 0;
            &::after {
                content: "";
                width: 40px;
                height: 40px;
                display: inline-block;
                background-image: url("../img/sprites/arrow_green_right.svg");
                background-position: center;
                background-repeat: no-repeat;
            }

            @include below(768px) {
                display: none;
            }
        }

        .swiper-pagination {
            display: none;

            @include below(768px) {
                display: block;
            }
            &.swiper-pagination-horizontal {
                bottom: 0;
            }

            .swiper-pagination-bullet {
                width: 11px;
                height: 11px;
                margin: 0 9.5px !important;
                background-color: #C4C4C4;
                opacity: 1;
            }

            .swiper-pagination-bullet-active {
                background-color: #00D54B;
            }
        }
    }
 
    &__item {
        display: flex;
        flex-direction: column;
        padding: 45px 45px 41px 41px;
        border-radius: 0px 50px;
        background-color: #FFF;
        box-shadow: 0px 0px 40px 0px rgba(201, 205, 220, 0.49);

        &.swiper-slide {
            display: flex;
            flex-direction: column;
            height: initial;
        }
 
        @include below(768px) {
            padding: 41px 20px 43px 20px;
        }
    }

    &__item-head {
        display: flex;
        justify-content: space-between;
    }

    &__head-buttons {
        display: flex;
        button {
            display: flex;
            flex-direction: column;
            align-items: center;
            color:#00D54B;
            font-size: 14px;
            font-weight: 700;
            line-height: 30px;

            &:nth-of-type(2) {
                color: #FE3C3C;
                margin-left: 8px;
            }
        }

        svg {
            width: 30px;
            height: 30px;

            @include below(540px) {
                width: 20px;
                height: 20px;
            }
        }
    }

    &__icons {
        display: flex;
        margin: 0 -3px 10px;
        color: #00D54B;

        &_orange {
            color: #FE823C;
        }

        svg {
            width: 25px;
            height: 25px;
            margin: 0 3px;
        }
    }

    &__name {
        font-size: 20px;
        font-weight: 700;
        line-height: 30px; 
        text-transform: uppercase;
    }

    &__data {
        color: #00D54B;
        font-size: 12px;
        font-weight: 700;
        line-height: 30px;
        text-transform: uppercase;
    }

    &__text {
        font-size: 16px;
        font-weight: 400;
        line-height: 120%; 
        margin: 10px 0;

        p {
            &:nth-child(2){
                margin: 10px 0;
            }
        }
    }

    &__admin {
        margin-top: auto;
        display: flex;
    }

    &__admin-icon {
        margin-right: 14px;
        svg {
            width: 24px;
            height: 24px;
        }
    }

    &__admin-title {
        color: #01D5C8;
        font-size: 16px;
        font-weight: 700;
        line-height: 30px; /* 187.5% */
        text-transform: uppercase;
    }

    &__admin-text {
        color: #B6B6B6;
        font-size: 14px;
        font-weight: 500;
        line-height: 120%;
    }

    &__inner {
        .reviews__item {
            width: 100%;

            &:not(:last-child) {
                margin-bottom: 20px;
            }
        }
    }

    &__btn {
        display: block;
        width: 100%;
        max-width: 275px;
        margin: 60px auto 0;
        padding: 17px 0 19px;
        font-size: 18px;
        text-transform: uppercase;
        border-radius: 100px;

        @include below(768px) {
            margin: 80px auto 0;
        }

        &_second {
            @include below(768px) {
                margin: 40px auto 0;
            }
        }

        @include below(479px) {
            max-width: initial;
        }
    }
}