@import "main-module";

.personal {

    &__head {
        display: flex;
        align-items: center;
        margin-bottom: 25px;

        svg {
            width: 39px;
            height: 39px;
            margin-left: 20px;

            @include below(768px) {
                display: none;
            }
        }
    }

    &__title {
        font-size: 27px;
        font-weight: 700;
        line-height: 130%; 

        @include below(768px) {
            font-size: 20px;
        }
    }

    &__text {
        font-size: 16px;
        font-weight: 400;
        line-height: 120%;
        margin-bottom: 50px;

        @include below(768px) {
            margin-bottom: 40px;
        }

        a {
            color: #00D54B;
            font-weight: 700;
            text-decoration: underline;
        }
    }

    &__wrap {
        display: flex;
        flex-wrap: wrap;
        margin: 0 -9px;

        @include below(992px) {
            margin: -15px 0 0;
        }
    }

    &__col {
        width: calc(50% - 18px);
        margin: 0 9px;
        background-color: #EDFFFC;
        padding: 75px 0 88px;

        @include below(992px) {
            width: 100%;
            margin: 15px 0 0;
            padding: 60px 30px;
        }

        @include below(768px) {
            padding: 50px 20px;
        }
    }

    &__form {
        width: 100%;
        max-width: 427px;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
    }

    &__form-title {
        text-align: center;
        font-size: 36px;
        font-weight: 700;
        line-height: 100%; 
        text-transform: uppercase;
        margin-bottom: 15px;

        @include below(1240px) {
            font-size: 30px;
        }

        @include below(992px) {
           font-size: 24px;
        }

        @include below(768px) {
            font-size: 20px;
        }
    }

    &__form-subtitle {
        text-align: center;
        font-size: 20px;
        font-weight: 600;
        line-height: 100%;
        text-transform: uppercase;

        @include below(992px) {
            font-size: 18px;
        }

        @include below(768px) {
            font-size: 16px;
        }
    }

    &__form-label {
        display: block;
        &:first-of-type {
            margin-top: 25px;
        }
        &:not(:last-child) {
            margin-bottom: 18px;
        }
    }

    &__form-input {
        height: 78px;
        @include below(768px) {
            border-radius: 20px;
            height: 70px;
        }
    }

    &__form-btn {
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: 140%; /* 25.2px */
        letter-spacing: 0.72px;
        text-transform: uppercase;
        border-radius: 100px;
        padding: 17px 0 19px;
        width: 100%;
        max-width: 275px;
        margin: 30px auto 0;

        @include below(768px) {
            margin: 20px 0 0;
            max-width: initial;
        }
    }

    &__form-text {
        padding: 28px 0;
        margin-bottom: 45px;
        border-bottom: 1px solid #D8D8D8;
        text-align: center;
        font-size: 16px;
        font-weight: 700;
        line-height: 120%;

        @include below(768px) {
           font-size: 14px;
           padding: 20px 0;
           margin-bottom: 20px;
        }

        a {
            color: #00D54B;
            text-decoration: underline;
        }
    }

    &__form-link {
        margin-top: 15px;
        color: #00D54B;
        text-align: center;
        font-size: 16px;
        font-weight: 700;
        line-height: 120%; 
        text-decoration-line: underline;
    }
}