@import "main-module";

.forecast-slider {
    max-width: 832px;
    position: relative;
    

    .swiper-button-prev,
    .swiper-button-next {
        width: 45px;
        height: 45px;
        border-radius: 50%;

        &:not(.swiper-button-disabled) {
            background-color: rgba(255, 255, 255, .3);
        }

        &::after {
            content: none;
        }

        svg {
            width: 12px;
            height: 12px;
        }
    }

    .swiper-button-next {
        right: -65px;
    }

    .swiper-button-prev {
        left: -65px;
    }

    &__slider {
        position: relative;
    }

    &__card {

        &-main {
            flex: auto;
            display: flex;
            flex-direction: column;
            padding-left: 18px;
        }

        &-wrap {
            border-radius: 0px 30px;
            border: 1px solid #E7E7E7;
            background: #FFF;
            box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.25);

            display: flex;
            align-items: stretch;
            padding: 14px 20px;

            
        }

        &-date {
            margin: 50px -40px 50px -50px;
            transform: rotate(-90deg);
            text-align: center;

            border-radius:  50px 0px;
            background: rgba(11, 224, 92, .3);
            padding: 3px 18px;
        }

        &-head {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 5px;
        }

        &-icon {
            width: 27px;
            height: 27px;
            img {
                width: 100%;
                height: 100%;
            }
        }

        &-time {
            color: #0BE05C;
            font-size: 18px;
            font-weight: 700;
            line-height: 24px; 
        }

        &-title {
            font-size: 16px;
            font-weight: 700;
            line-height: 100%; 
            color: #000;
        }

        &-geo {
            font-size: 11px;
            font-weight: 700;
            line-height: 24px;
            color: #979797;
            margin-bottom: 6px;
        }

        &-foot {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-top: auto;
        }

        &-count {
            font-size: 20px;
            font-style: normal;
            font-weight: 700;
            line-height: 24px;

            span {
                &:not(:last-child) {
                    color: #000;
                }

                &:last-child {
                    color: #00D54B;
                }
            }
        }

        &-buy {
            margin-left: 12px;
            font-size: 15px;
            font-weight: 700;
            line-height: normal;
            letter-spacing: 0.3px;
            color: #00D5C8;
            border-radius: 100px;
            border: 2px solid #00D5C8;
            width: 100px;
            padding: 5px 0;
            transition: background-color .3s linear, color .3s linear;

            &:hover:not(:active) {
                color: #fff;
                background-color: rgba(0, 213, 200, .5);
            }
        }
    }
}