@import "main-module";

.contacts {

    &__head {
        display: flex;
        align-items: center;
        margin-bottom: 50px;

        @include below(768px) {
            margin-bottom: 30px;
        }

        svg {
            width: 39px;
            height: 39px;
            margin-left: 20px;

            @include below(768px) {
               display: none;
            }
        }
    }

    &__title {
        font-size: 27px;
        font-weight: 700;
        line-height: 130%;

        @include below(768px) {
            font-size: 20px;
        }
    }

    &__row {
        display: flex;
        @include below(992px) {
            flex-wrap: wrap;
        }
    }

    &__col {
        width: 50%;
        font-size: 16px;
        line-height: 120%;

        @include below(992px) {
            width: 100%;
        }

        &:first-of-type {
            padding-right: 40px;
            border-right: 1px solid #D8D8D8;

            @include below(992px) {
               padding: 0 0 20px;
               border-right: none;
               border-bottom: 1px solid #D8D8D8;
            }
        }

        &:last-of-type {
            padding-left: 60px;

            @include below(992px) {
                padding: 20px 0 0;
            }
        }

        p {
            margin-bottom: 15px;

            @include below(768px) {
                margin-bottom: 10px;
            }
        }
    }

    &__info {
        margin: 50px 0;
        color: #00D54B;
        font-size: 15px;
        font-weight: 700;
        line-height: 160%;
        text-transform: uppercase;

        @include below(992px) {
            margin: 30px 0;
        }

        a {
            color: #00D54B;
        }
    }

    &__map {
        width: 100%;
        overflow: hidden;
        height: 490px;

        
        @include below(768px) {
            height: 380px;
        }

        .ymaps-2-1-79-ground-pane {
            filter: grayscale(100%);
            -webkit-filter: grayscale(100%);
            -moz-filter: grayscale(100%);
        }

        .ymaps-2-1-79-copyrights-pane {
            display: none;
        }
        
        .ymaps-2-1-79-map ymaps, .ymaps-2-1-79-map ymaps:after, .ymaps-2-1-79-map ymaps:before  {
            filter: grayscale(100%);
            -webkit-filter: grayscale(100%);
            -moz-filter: grayscale(100%);
        }
    }
}