@import "main-module";

.articles {
    background-color: #EDFFFC;
    padding: 90px 0 130px;

    @include below(768px) {
        padding: 75px 0 92px;
    }

    &__head {
        display: flex;
        justify-content: space-between;
        margin-bottom: 60px;

        
        @include below(768px) {
            margin-bottom: 50px;
        }
    }

    &__right {
        display: flex;
        align-items: center;
    }

    &__pagination {
        margin-right: 140px;
    }

    &__title {
        font-size: 50px;
        font-weight: 700;
        line-height: 100%;

        @include below(1024px) {
            font-size: 36px;
        }

        @include below(479px) {
            text-align: center;
        }

        span {
            color: #00D54B;
        }
    }

    &__head-link {
        color: #CCC;
        text-align: right;
        font-size: 14px;
        font-weight: 700;
        line-height: normal;
        letter-spacing: 0.28px;
        text-decoration-line: underline;
    }

    &__wrap {
        position: relative;
    }

    &__row {
        display: flex;
        align-items: stretch;
        margin: 0 -10px;
    }

    &__item {
        width: calc(33.333% - 20px);
        margin: 0 10px;
        display: flex;
        @include below(540px) {
            flex-direction: column;
            background-color: #fff;
            border-radius: 0px 50px;
            box-shadow: 0px 0px 40px 0px rgba(201, 205, 220, 0.49);
        }
    }

    &__img {
        width: 100%;
        max-width: 203px;
        flex-shrink: 0;
        margin-right: 29px;

        @include below(540px) {
            max-width: initial;
            margin: 0;
        }

        img {
            border-radius: 0 50px;

            @include below(540px) {
                width: 100%;
            }
        }
    }

    &__body {
        @include below(540px) {
            padding: 24px 20px 53px;
        }
    }

    &__data {
        color: #B6F6CE;
        font-family: Raleway;
        font-size: 12px;
        font-weight: 700;
        line-height: 30px;
        text-transform: uppercase;
    }

    &__name {
        margin: 12px 0 5px;
        font-size: 18px;
        font-weight: 700;
        line-height: 105%;
    }

    &__text {
        font-size: 14px;
        font-weight: 500;
        line-height: 120%;
        margin-bottom: 9px;
    }

    &__link {
        color: #00D54B;
        font-size: 14px;
        font-weight: 700;
        line-height: normal;
        letter-spacing: 0.28px;
        text-decoration-line: underline;
    }

    &__btn {
        margin-top: 50px;
        padding: 17px 30px 19px;
        border-radius: 100px;
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: 140%;
        letter-spacing: 0.72px;
        text-transform: uppercase;

        display: none;
    }
}