@import "main-module";

.partner {
    &__row {
        display: flex;
        align-items: stretch;
        margin: 0 -10px; 
        
        @include below(1240px) {
            flex-wrap: wrap;
            margin: -20px -10px 0;
        }

        @include below(768px) {
            margin: -20px 0 0;
        }
    }

    &__item {
        display: flex;
        flex-direction: column;
        width: calc(33.333% - 20px);
        margin: 0 10px;
        border-radius: 44px 44px 0px 44px;
        background-color: #CCF7E8;   
        padding: 30px;

        @include below(1240px) {
            width: calc(50% - 20px);
            margin: 20px 10px 0;
            padding: 20px;
        }

        @include below(768px) {
            width: 100%;
            margin: 20px 0 0;
        }

        &-btn {
            font-size: 18px;
            font-weight: 700;
            line-height: 140%; 
            letter-spacing: 0.72px;
            text-transform: uppercase;
            display: block;
            padding: 17px 0 19px;
            border-radius: 100px;
            background: linear-gradient(180deg, #00D54B 0%, #00D5C8 100%);
            width: 100%;
            max-width: 275px;
            margin: auto auto 0;

            @include below(768px) {
                margin: 20px 0 0;
            }
        }
    }

    &__top {
        display: flex;
        justify-content: space-between;
        padding-bottom: 20px;
        border-bottom: 1px solid #00D5A2;

        &-title {
            font-size: 28px;
            font-weight: 700;
            line-height: 100%; 

            @include below(1340px) {
                font-size: 24px;
            }

            @include below(1024px) {
                font-size: 20px;
            }
        }

        a {
            color: #00D5A2;
            font-size: 14px;
            font-weight: 700;
            line-height: 100%; 
            text-decoration-line: underline;
            margin-right: 12px;
        }

        &-icon {
            display: flex;
            align-items: center;
            margin-left: auto;

            svg {
                width: 34px;
                height: 34px;

                @include below(1024px) {
                    width: 26px;
                    height: 26px;
                }
            }
        }
    }

    &__center {
        margin: 15px 0 0;
        &-text {
            display: flex;
            justify-content: space-between;
            color: #009684;
            font-size: 18px;
            font-weight: 700;
            line-height: 100%; 

            @include below(768px) {
                font-size: 16px;
            }

            &:not(:last-of-type) {
                margin-bottom: 15px;
            }

            &_sm {
                margin: 0;
                display: block;
                color: #00D5A2;
                font-size: 13px;
                font-weight: 500;
                line-height: normal;
                @include below(768px) {
                    font-size: 12px;
                }
                strong {
                    font-size: 20px;
                }
            }

            span {
                font-size: 34px;
                display: inline-block;
                margin-left: auto;

                @include below(1024px) {
                    font-size: 25px;
                }
            }
        }
    }

    &__label {
        position: relative;
        display: block;
        width: 100%;
        margin-top: 25px;

        &_first {
            margin-top: 35px;
        }

        &-input {
            width: 100%;
            height: 78px;
            padding: 19px 40px;
            background-color: #fff;
            border-radius: 50px;
            font-size: 18px;
            font-weight: 600;
            line-height: 30px;
            &::placeholder {
                color: #B6B6B6;
            }

            @include below(1024px) {
                height: 60px;
                padding: 19px;
            }
        }

        &-icon {
            position: absolute;
            top: 14px;
            right: 16px;
            height: 50px;
            width: 50px;
            border-radius: 50%;
            background: linear-gradient(180deg, #00D54B 0%, #00D5C8 100%);
            display: flex;
            justify-content: center;
            align-items: center;

            @include below(1024px) {
                top: 5px;
                right: 7px;
            }
        }
    }

    &__bottom {
        margin-top: 26px;
        display: flex;
        justify-content: space-between;

        a {
            color: #00D5A2;
            font-size: 14px;
            font-weight: 700;
            line-height: 100%;
            text-decoration-line: underline;
        }

        p {
            color: #00D5A2;
            font-size: 13px;
            font-weight: 500;
            line-height: normal;
        }

        &-right {
            button {
                svg {
                    width: 20px;
                    height: 20px;
                }
            }
        }

    }

    &__table-title {
        text-align: center;
        font-size: 36px;
        font-weight: 700;
        line-height: 100%; 
        text-transform: uppercase;
        margin: 90px 0 32px;

        @include below(1240px) {
            font-size: 30px;
        }

        @include below(992px) {
            font-size: 22px;
            margin: 50px 0 32px;
        }
    }

    &__table-content {
        @include below(768px) {
            max-width: 900px;
            overflow-x: auto;
            overflow-y: hidden;
        }
        
        &_first {
           
            table {
                th {
                    width: 25%;
                }
                tbody {
                    td {
                        width: 25%;
                    }
                }
            }
        }

        &_second {

            table {
                th {
                    width: 20%;
                }
                tbody {
                    td {
                        width: 20%;
                    }
                }
            }
        }

        &_third {
            table {
               thead {
                    th {
                        width: 33.333%;
                    }
               }

                tbody {
                    td {
                        text-align: center;
                        text-transform: uppercase;
                        width: 33.333%;
                    }
                }
            }
        }
        
        table {
            width: 100%;
            thead {
                th {
                    background-color: #EDFFFC;;
                    padding: 16px 0 11px;
                    color: #00D54B;
                    text-align: center;
                    font-family: Raleway;
                    font-size: 15px;
                    font-style: normal;
                    font-weight: 700;
                    line-height: 100%;
                    text-transform: uppercase;     
                    
                    @include below(768px) {
                       padding: 15px;
                       font-size: 13px;
                    }
                }
            }

            tbody {
                td {
                    padding: 25px 0;
                    color: #000;
                    text-align: center;
                    font-family: Raleway;
                    font-size: 20px;
                    font-style: normal;
                    font-weight: 700;
                    line-height: 100%; 
                    border-bottom: 1px solid #D8D8D8;
                    span {
                        font-size: 16px;
                    }

                    @include below(992px) {
                        font-size: 16px;
                    }

                    @include below(768px) {
                        padding: 20px;
                    }
                }

            }
        }
    }
}