@import "main-module";

.video-block {
    .video-js,
    video {
        width: 100%;
        height: 100%;
        object-fit: cover;
        cursor: pointer;
    }

    .video-js {
        overflow: hidden;
        padding-bottom: 29.5%;

        @include below(1240px) {
            padding-bottom: 50%;
        }

        @include below(768px) {
            padding-bottom: 57%;
        }

        .vjs-big-play-button {
           width: 74px;
           height: 74px;
           border-radius: 50%;
           background-color: transparent;
           line-height: 1.4;
           font-size: 5em;
           left: 52%;

            @include below(1340px) {
                width: 60px;
                height: 60px;
                font-size: 3em;
                line-height: 2;
            }

            @include below(768px) {
                width: 52px;
                height: 52px;
                font-size: 3em;
                line-height: 1.6;
            }

            @include below(479px) {
                left: 54%;
            }
        }
    }

    .vjs-poster {
        img {
            object-fit: cover;
        }
    }
}