@import "main-module";

.breadcrumbs {
    background-color: #CCF7DB;
    &__list {
        display: flex;
        align-items: center;
        padding: 15px 0 13px;
    }

    &__item {
        font-size: 14px;
        font-weight: 700;
        line-height: 130%;
        
        &:not(:last-child) {
            &::after {
                content: ">";
                display: inline-block;
                margin: 0 10px; 
                color: #00D54B;
            }
        }

        a {
            color: #000;
        }

        span {
            color: #B6B6B6;
            font-weight: 500;

            br {
                display: none;

                @include below(575px) {
                    display: block;
                }
            }
        }
    }
}