@import "main-module";

.tabs {
    margin: 30px 0 35px;

    &__list {
        display: flex;
        align-items: center;
        background-color: #CCF7DB;

        @include below(992px) {
            overflow-y: hidden;
            overflow-x: auto;
        }
    }

    &__item {
        flex: auto; 
    }

    &__button {
        position: relative;
        padding: 22px 0;
        width: 100%;
        color: #00D54B;
        text-align: center;
        font-size: 14px;
        font-weight: 700;
        line-height: normal;
        letter-spacing: 0.28px;
        text-transform: uppercase;

        @include below(992px) {
            white-space: nowrap;
            padding: 22px 25px;
        }

        &::after {
            content: "";
            width: 1px;
            height: 38px;
            background-color: #00D54B;
            position: absolute;
            right: 0;
            top: 11px;
        }

        &_last {
            &::after {
                display: none;
            }
        }

        &_active {
            background-color: #00D54B;
            color: #fff;
        }
    }
}