@import "main-module";

.footer {
    &__top {
        background-color: $color_black;
    }

    &__head {
        color: #fff;
        text-align: center;
        font-size: 19px;
        font-weight: 700;
        line-height: 100%;
        text-transform: uppercase;
        padding: 74px 0 50px;
        border-bottom: 1px solid #fff;

        @include below(1024px) {
            padding: 60px 0 32px;
        }
    }

    &__center {
        display: flex;
        padding: 50px 0;
        @include below(1024px) {
            flex-wrap: wrap;
            padding: 32px 0;
        }
    }

    &__logo {
        width: 300px;
        margin-right: 88px;

        @include below(1300px) {
            width: 150px;
            margin-right: 32px;
        }

        @include below(1024px) {
            order: 1;
        }

        @include below(550px) {
            width: 220px;
        }
    }

    &__pay {
        margin-right: 100px;
        @include below(1024px) {
            order: 3;
            width: 100%;
            margin-right: 0;
            padding: 32px 0 0;
        }
        
    }

    &__pay-label {
        color: #fff;
        font-size: 12px;
        font-weight: 500;
        line-height: 22px;
        margin-bottom: 10px;

        @include below(550px) {
            font-size: 14px;
        }
    }

    &__pay-text {
        color: #ABABAB;
        font-size: 12px;
        font-weight: 400;
        line-height: 22px;

        @include below(550px) {
            display: none;
        }
    }

    &__pay-logo {
        display: flex;
        align-items: center;
        margin-bottom: 20px;
        img {
            &:not(:last-child) {
                margin-right: 18px;
            }
        }
    }

    &__bottom {
        background-color: #1B1B1B;
        padding: 20px;
        text-align: center;
        color: #ABABAB;
        font-size: 12px;
        font-weight: 400;
        line-height: 22px;
        span {
            text-decoration-line: underline;
        }
    }

    &__menu {
        flex: auto;
        display: flex;
        justify-content: space-between;
        
        @include below(1024px) {
            order: 2;
        }

        @include below(768px) {
            width: 100%;
            padding: 32px 0;
            border-bottom: 1px solid #fff;
            flex-wrap: wrap;
        } 

        ul {
            &:not(:last-child) {
                margin-right: 24px;
                @include below(550px) {
                    margin-right: 0;
                    width: 48%;
                }
            }

            &:last-child {
                @include below(550px) {
                    width: 100%;
                    margin-top: 32px;
                    padding-top: 32px;
                    border-top: 1px solid #fff;
                }
            }
        }

        li {
            &:not(:last-child) {
                margin-bottom: 5px;
            }
        }

        li a {
            color: #fff;
            font-size: 12px;
            font-weight: 500;
            line-height: 22px;

            @include below(550px) {
                font-size: 14px;
                line-height: 180%; 
            }

            &:hover {
                text-decoration: underline;
            }
        }
    }
}