@import "main-module";

.buy {
    padding: 45px 0;
    background-color: #00D5C8;

    &_second {
        padding: 45px 0 180px;
        background: #000;
        position: relative;
    }

    &__img {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        z-index: 0;

        @include below(992px) {
            display: none;
        }

        img {
            width: 100%;
            height: 100%;
        }
    }

    @include below(992px) {
        padding: 65px 0 75px;
        background:  linear-gradient(180deg, #00D54B 0%, #00D5C8 100%);;
    }

    &__row {
        position: relative;
        z-index: 10;
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include below(1240px) {
            flex-direction: column;
            justify-content: center;
        }
    }

    &__title {
        color: #FFF;
        font-size: 36px;
        font-style: normal;
        font-weight: 500;
        line-height: 120%;

        
        @include below(1240px) {
            text-align: center;
            margin-bottom: 30px;
            line-height: 100%;
        }
    }

    &__btn {
        color: #fff;
        font-size: 18px;
        font-weight: 700;
        line-height: 140%;
        letter-spacing: 0.72px;
        text-transform: uppercase;
        padding: 17px 0 19px;
        border-radius: 100px;
        border: 2px solid #FFF;
        width: 100%;
        max-width: 270px;

        @include below(479px) {
            max-width: initial;
        }
    }
}