@import "main-module";

.advantages {
    position: relative;
    color: #FCFCFC;
    background-color: #000;


    &_second {
        padding: 85px 0 70px;
        color: #000;
        background-color: #fff;

        @include below(768px) {
            padding: 50px 0;
        }
    }

    &__head {
        bottom: -100px;
        position: relative;
        padding: 105px 0 250px;

        @include below(992px) {
            bottom: 0;
            padding: 105px 0;
        }

        @include below(768px) {
            padding: 80px 0;
        }
    }

    &__head-img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        img {
            display: block;
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
    }

    &__content {
        position: relative;
        color: #fff;
        z-index: 10;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__content-title {
        display: flex;
        align-items: center;
        color: #fff;

        span {
            font-size: 50px;
            font-weight: 700;
            line-height: 100%;
            text-decoration-line: underline;

            @include below(1240px) {
                font-size: 42px;
            }

            @include below(1024px) {
                font-size: 36px;
                text-align: center;
            }
        }

        svg {
            width: 39px;
            height: 39px;
            margin-left: 20px;

            @include below(1240px) {
                width: 30px;
                height: 30px;
            }

            @include below(992px) {
                display: none;
            }
        }
    }

    &__content-subtitle {
        text-align: center;
        margin: 20px 0;
        color: #FFF;
        text-shadow: 0px 0px 15px rgba(0, 0, 0, 0.40);
        font-size: 79px;
        font-weight: 700;
        line-height: 100%; 
        
        span {
            color: #0BE05C;
        }

        @include below(1340px) {
            font-size: 65px;
        }

        @include below(1024px) {
            font-size: 50px;
            margin: 20px 0 35px;
        }
    }

    &__content-text {
        &:first-of-type {
            text-align: center;
            font-size: 20px;
            font-weight: 500;
            line-height: 140%; 
            margin-bottom: 20px;
            @include below(768px) {
                font-size: 18px;
            }
        }

        &:last-of-type {
            color: #0BE05C;
            text-align: center;
            font-size: 16px;
            font-weight: 700;
            line-height: 160%; 
            text-transform: uppercase;

            @include below(768px) {
                font-size: 15px;
            }
        }
    }

    &__btn {
        color: #FFF;
        font-size: 18px;
        font-weight: 700;
        line-height: 140%; 
        letter-spacing: 0.72px;
        text-transform: uppercase;
        width: 100%;
        max-width: 275px;
        border-radius: 100px;
        margin-top: 30px;
        padding: 17px 0 19px;

        @include below(479px) {
            margin-top: 40px;
            max-width: initial;
        }
    }

    &__inner {
        position: relative;
        padding: 212px 0 86px;
        // background-color: #000;

        @include below(992px) {
            padding: 67px 0 96px;
        }
    }

    &__img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 5;

        img {
            display: block;
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
    }

    &__wrap {
        position: relative;
        z-index: 10;
    }

    &__suptitle {
        color: #00D54B;
        font-size: 22px;
        font-weight: 700;
        line-height: 100%; 
        text-transform: uppercase;
        margin-bottom: 40px;

        @include below(768px) {
            font-size: 15px;
            text-align: center;
        }
    }

    &__top {
        display: flex;
        margin-bottom: 50px;

        @include below(1240px) {
            flex-direction: column;
        }

        @include below(768px) {
            margin-bottom: 40px;
        }
    }

    &__top-title {
        width: 100%;
        max-width: 550px;
        font-family: Raleway;
        font-size: 27px;
        font-style: normal;
        font-weight: 700;
        line-height: 130%;
        margin-right: 40px;

        @include below(1240px) {
            max-width: initial;
            margin: 0 0 40px;
        }

        @include below(768px) {
            text-align: center;
        }
    }

    &__top-text {
        font-size: 16px;
        font-weight: 500;
        line-height: 140%;

        @include below(768px) {
            font-size: 14px;
            text-align: center;
        }

        p {
            &:first-of-type {
                margin-bottom: 20px;
            }
        }
    }

    &__title {
        color: #FFF;
        font-size: 50px;
        font-style: normal;
        font-weight: 700;
        line-height: 100%;
        margin-bottom: 40px;

        &_second {
            color: #000;
        }

        @include below(1024px) {
            font-size: 36px;
        }

        @include below(768px) {
            text-align: center;
        }
    }

    &__row {
       display: flex;
       align-items: stretch;
       flex-wrap: wrap;
       margin: 0 -10px;

       .swiper-wrapper {
            display: flex;
            align-items: stretch;
            flex-wrap: wrap;

            @include below(1240px) {
                margin: -20px -10px 0;
            }

            @include below(768px) {
                margin: 0;
            }
        }

        &_second {
            .advantages__item {
                border: 1px solid #D8D8D8;
            }
        }
    }

    &__nav {
        .swiper-pagination {
            display: none;

            @include below(768px) {
                display: block;
            }
        }
    }

    &__item {
        width: calc(25% - 20px);
        margin: 0 10px;
        display: flex;
        flex-direction: column;
        padding: 25px 20px 30px 32px;
        border-radius: 0px 50px;
        border: 1px solid rgba(255, 255, 255, 0.50);

        @include below(768px) {
            padding: 25px 20px 30px;
        }

        &.swiper-slide {
            width: calc(25% - 20px);
            display: flex;
            flex-direction: column;
            height: initial;

            @include below(1240px) {
                width: calc(33.333% - 20px);
                margin: 20px 10px 0;
            }

            @include below(992px) {
                width: calc(50% - 20px);
                margin: 20px 10px 0;
            }

            @include below(768px) {
                width: 100%;
                margin: 0;
            }
        }
        &_first {
            background-color: #0BE05C;
            color: #FCFCFC;

            .advantages__item-numb {
                color: #FCFCFC;
            }

            @include below(768px) {
                background: linear-gradient(180deg, #00D5C8 0%, #00D54B 100%);
            }
        }
    }

    &__item-numb {
        color: #00D54B;
        font-size: 68px;
        font-weight: 700;
        line-height: 100%; 
        text-transform: uppercase;
    }

    &__item-title {
        font-size: 26px;
        font-weight: 700;
        line-height: 100%;
        margin: 20px 0;

        @include below(768px) {
            margin: 10px 0;
        }
    }

    &__item-text {
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: 140%;
    }
}