@import "main-module";

.algorithm {

    &__row {
        display: flex;
        flex-wrap: wrap;
    }

    &__left { 
        width: 40%;
        position: relative;

        @include below(1700px) {
            width: 33.333%;
        }

        @include below(1240px) {
            width: 100%;
            height: 568px;
        }

    }

    &__title {
        position: absolute;
        top: 183px;
        left: 242px;
        color: #FFF;
        text-shadow: 0px 0px 15px rgba(0, 0, 0, 0.40);
        font-size: 50px;
        font-weight: 700;
        line-height: 100%; 

        @include below(1800px) {
            left: 160px
        }

        @include below(1700px) {
            left: 100px;
        }

        @include below(1600px) {
            left: 80px;
            font-size: 45px;
        }

        @include below(1500px) {
            left: 40px;
        }

        @include below(1340px) {
            font-size: 40px;
        }

        @include below(1240px) {
            text-align: center;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        @include below(768px) {
            font-size: 36px;
            br {
                display: none;
            }
        }

        span {
            color: #00D54B;
        }
    }

    &__img {
        width: 100%;
        height: 100%;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__list {
        width: 22%;

        @include below(1700px) {
            width: 33.333%;
        }

        @include below(1240px) {
            width: 50%;
        }

        @include below(768px) {
            width: 100%;
        }
    }

    &__item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: #000;
        padding: 60px 33px 35px 39px;
        font-size: 20px;
        font-weight: 700;
        line-height: 140%; 
        text-decoration-line: underline;
        transition: color .3s linear, background-color .3s linear;
        &:hover {
            background-color: rgba(0, 213, 200, 0.10);
            color: #00867E;
        }

        @include below(1600px) {
            padding: 60px 30px 30px;
        }

        @include below(1500px) {
            padding: 40px 30px;
        }

        @include below(768px) {
            font-size: 18px;
            &:hover {
                background-color: rgba(0, 213, 75, 0.20);
                color: #00D54B;
            }

            &:not(:last-child) {
                border-bottom: 1px solid #CCF7DB;
            }
        }

        @include below(540px) {
            padding: 40px 15px 30px;
        }

        span {
            max-width: 305px;

            @include below(768px) {
                max-width: initial;
            }
        }

        svg {
            width: 39px;
            height: 39px;
            flex-shrink: 0;
            margin-left: 15px;
        }
    }

    &__right {
        width: 38%;
        padding: 60px 273px 60px 70px;
        background-color: #00D5C8;
        color: #fff;

        @include below(1800px) {
            padding: 60px 160px 60px 70px;
        }

        @include below(1700px) {
            width: 33.333%;
            padding: 60px 100px 60px 70px;
        }

        @include below(1600px) {
            padding: 60px 80px 60px 70px;
        }

        @include below(1500px) {
            padding: 40px;
        }

        @include below(1240px) {
            width: 50%;
        }

        @include below(768px) {
            width: 100%;
            background: linear-gradient(180deg, #00D54B 0%, #000 100%);
        }

        @include below(540px) {
            padding: 55px 15px 85px;
        }
    }

    &__content-title {
        font-size: 20px;
        font-weight: 700;
        line-height: 140%; 
        margin-bottom: 30px;

        @include below(768px) {
            font-size: 16px;
            line-height: 120%;
        }

        a {
            color: #fff;
            text-decoration: underline;
        }
    }

    &__content-text {
        font-size: 17px;
        font-weight: 500;
        line-height: 130%;

        @include below(768px) {
            font-size: 14px;
            line-height: 120%;
        }
    }

    &__back-link {
        display: none;
        margin-bottom: 20px;
        svg {
            width: 39px;
            height: 39px;
        }
    }
}