@import "main-module";

.section {
    padding: 70px 0 140px;

    @include below(768px) {
        padding: 45px 0 70px;
    }

    &_second {
        padding: 70px 0 0;

        @include below(768px) {
            padding: 45px 0 0;
        }
    }

    &__title {
        font-size: 50px;
        font-weight: 700;
        line-height: 100%;
        margin-bottom: 26px;

        @include below(1024px) {
            font-size: 36px;
        }

        br {
            @include below(768px) {
                display: none;
            }
        }
    }

    &__text {
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 120%;
        p {
            &:last-of-type {
               margin-top: 20px;
            }
        }
        a {
            font-weight: 700;
            color: #00D54B;
            text-decoration: underline;
        }
    }

    &__btn {
        margin-top: 20px;
        border-radius: 100px;
        padding: 17px 48px 19px;
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: 140%;
        letter-spacing: 0.72px;
        text-transform: uppercase;
    }

    &__img {
        margin-top: 30px;
        display: block;
        img {
            width: 100%;
            height: 100%;

            @include below(992px) {
                border-radius: 0 25px;
            }
        }
    }

    &__row {
        display: flex;
        align-items: center;
        margin: 14px 0 42px;

        @include below(992px) {
            flex-direction: column;
            align-items: start;
            margin: 30px 0 50px;
        }
    }

    &__wrap {
        &-btn {
            text-transform: uppercase;
            font-size: 18px;
            font-weight: 700;
            line-height: 140%;
            letter-spacing: 0.72px;
            border-radius: 100px;
            padding: 17px 0 19px;
            width: 100%;
            max-width: 275px;
            margin-top: 45px;

            @include below(479px) {
                max-width: initial;
            }
        }
    }

    &__row-logo {
        width: 100%;
        max-width: 229px;
    }

    &__row-text {
        font-size: 19px;
        font-weight: 700;
        line-height: 100%; 
        text-transform: uppercase;
        margin-right: 50px;

        @include below(992px) {
            margin: 0 0 20px;
        }
    }

    &__info {
        background-color: #EDFFFC;
        padding: 66px 0 64px;

        &.section__info_second {
            padding: 40px 65px;
            margin: 40px 0;

            .section__info-title {
                margin: 0;
            }

            @include below(768px) {
                padding: 30px;
                margin: 30px 0;
            }
        }

        @include below(768px) {
            padding: 45px 0 50px;
        }
    }

    &__info-flex {
        display: flex;
        align-items: center;
        margin-bottom: 25px;

        @include below(768px) {
            flex-direction: column;
            align-items: start;
        }
    }

    &__info-icon {
        svg {
            width: 34px;
            height: 34px;
            margin-right: 15px;

            @include below(768px) {
                margin: 0 0 20px;
            }
        }
    }

    &__info-suptitle {
        color: #00D54B;
        font-size: 15px;
        font-weight: 700;
        line-height: 120%; 
        text-transform: uppercase;
    }

    &__info-title {
        font-size: 27px;
        font-weight: 700;
        line-height: 130%; 
        margin-bottom: 25px;

        a {
            color: #00D54B;
            text-decoration: underline;
        }

        span {
            color: #00D54B;
        }

        @include below(768px) {
            font-size: 25px;
            line-height: 100%;
            margin-bottom: 30px;
        }
    }

    &__info-text {
        font-size: 16px;
        font-weight: 400;
        line-height: 140%;

        @include below(768px) {
            line-height: 110%;
        }

        p {
            margin: 15px 0;

            strong {
                text-transform: uppercase;
            }


            span {
                color: #00D54B;
                font-weight: 700;
            }
        }
       
    }

    &__info-list {
        margin: 20px 0;

        li {
            font-size: 16px;
            line-height: 140%;
            margin-bottom: 5px;

            &::before {
                content: ">";
                display: inline-block;
                color: #00D54B;
                margin-right: 6px;
            }
        }
    }
    
    &__item {
        padding: 70px 0 55px;
        border-bottom: 1px solid #000;

        @include below(768px) {
            padding: 60px 0 45px;
        }

        &:last-child {
            padding: 70px 0 0;
            border: none;

            @include below(768px) {
                padding: 60px 0 0;
            }
        }

        &_first {
            padding: 0 0 55px;

            @include below(768px) {
                padding: 0 0 45px;
            }
        }

        p {
          margin-bottom: 15px;
          font-size: 16px;
          line-height: 140%;
          
            @include below(768px) {
                line-height: 110%;
            }

            span {
                display: inline-block;
                color: #00D54B;
                font-size: 15px;
                font-weight: 700;
                line-height: 120%;
                text-transform: uppercase;
            }
        }

        ul {
            margin: 20px 0;
        
            li {
                font-size: 16px;
                line-height: 120%;
                &:not(:last-child) {
                    margin-bottom: 10px;
                }
    
                &::before {
                    content: ">";
                    display: inline-block;
                    color: #00D54B;
                    margin-right: 6px;
                }
            }
        }

    }

    &__item-title {
        font-size: 50px;
        font-weight: 700;
        line-height: 100%;
        margin-bottom: 40px;

        @include below(1240px) {
            font-size: 40px;
        }

        @include below(1024px) {
            font-size: 36px;
            margin-bottom: 30px;
        }

        &_sm {
            font-size: 27px;
            font-style: normal;
            font-weight: 700;
            line-height: 130%; 

            a {
                color: #000;
            }

            @include below(1240px) {
                font-size: 24px;
            }
    
            @include below(1024px) {
                font-size: 20px;
                margin-bottom: 30px;
            }
        }

        svg {
            width: 39px;
            height: 39px;
            margin-left: 10px;
            display: inline-block;
            transform: translateY(7px);

            @include below(1240px) {
                width: 30px;
                height: 30px;

                @include below(992px) {
                    display: none;
                }
            }
        }   
    }

    &__item-text {
        margin: 30px 0;
        p {
            display: flex;

            svg {
                flex-shrink: 0;
                display: inline-block;
                margin: 5px 20px 0 65px;
                width: 14px;
                height: 14px;

                @include below(992px) {
                    margin: 5px 15px 0 30px;
                }

                @include below(768px) {
                    margin: 5px 15px 0 15px;
                }
            }

            strong {
                display: inline-block;
                margin: 0px 20px 0 65px;
                color: #00D5C8;
                font-size: 20px;
                font-weight: 700;
                line-height: 120%;

                @include below(992px) {
                    margin: 0px 15px 0 30px;
                }

                @include below(768px) {
                    margin: 0 15px 0 15px;
                }
            }
        }
    }
    &__item-row {
        display: flex;
        align-items: stretch;
        flex-wrap: wrap;
        padding-top: 30px;
        margin: -25px -30px 0;

        @include below(768px) {
            margin: -20px -20px 0;
        }
    }

    &__item-logo {
        width: calc(16.666% - 60px);
        margin: 25px 30px 0;

        @include below(1240px) {
            width: calc(20% - 60px);
            margin: 25px 30px 0;
        }

        @include below(1024px) {
            width: calc(25% - 60px);
            margin: 25px 30px 0;
        }

        @include below(992px) {
            width: calc(33.333% - 60px);
            margin: 25px 30px 0;
        }

        @include below(768px) {
            width: calc(50% - 40px);
            margin: 20px 20px 0;
        }
    }

    &__bottom {
        h2 {
            font-size: 27px;
            font-weight: 700;
            line-height: 130%;
            margin-bottom: 25px;
    
            @include below(768px) {
                font-size: 25px;
                line-height: 100%;
            }
    
            a {
                color: #00D54B;
                text-decoration: underline;
            }
        }

        p {
            color: #000;
            font-size: 16px;
            font-weight: 400;
            line-height: 120%;
            a {
                color: #00D54B;
                text-decoration: underline;
            }
        }
    }

}