@import "main-module";

.textarea {
    width: 100%;
    border-radius: 50px;
    border: 2px solid #CCEFF7;
    background: #FFF;
    padding: 19px 40px;
    font-family: Raleway;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 30px;
    color: #000;
    resize: none;

    &::placeholder {
        color: #B6B6B6;
    }

    &:disabled {
        background-color: #F7F9FB;
        border-color: #F7F9FB;
        color: #808191;
    }
}