@import "main-module";

.intro {
    position: relative;
    margin-top: -162px;
    padding: 215px 0 116px;
    overflow: hidden;
    
    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;

        img {
            object-fit: cover;
            width: 100%;
            height: 100%;
        }
    }

    &__top {
        display: flex;
    }

    &__bottom {
        display: flex;
        align-items: flex-end;
        margin-top: 40px;
    }

    &__swiper {
        margin-left: 120px;
    }
    
    &__content {
        max-width: 782px;
    
        &-right {
            flex: auto;
            display: flex;
            justify-content: flex-end;
            position: relative;
        }
    }

    &__phone {
        position: absolute;
        left: -170px;
        top: -80px;
        z-index: -1;
        width: 700px;
        height: 700px;
    }

    &__order {
        border-radius: 0px 50px;
        background: #00D5C8;
        box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.25);
        max-width: 267px;
        padding: 28px 26px;
        color: #fff;

        &-title {
            font-size: 22px;
            font-weight: 700;
            line-height: 100%;
            text-transform: uppercase;
            margin-bottom: 14px;
        }

        &-text {
            font-size: 14px;
            font-weight: 600;
            line-height: 110%;
        }

        &-link {
            width: 40px;
            height: 40px;
            margin-left: auto;
            svg {
                width: 100%;
                height: 100%;
            }
        }
    }

    &__title {
        color: #fff;
        font-size: 68px;
        font-weight: 700;
        line-height: 100%;
        margin-bottom: 17px;

        span {
            color: #00D54B;
        }
    }

    &__text {
        color: #fff;
        font-size: 24px;
        font-weight: 500;
        line-height: 140%;
        margin-bottom: 75px;
        span {
            color: #00D54B;
        }
    }

    &__slider {
        max-width: 600px;

        .irs .irs-line {
            background-color: #f4f3fA;
            border-radius: 20px;
            height: 7px;
            top: 27px;
        } 

        .irs .irs-bar.irs-bar--single {
            background: linear-gradient(90deg, #00D5C8 0%, #0BE05C 96.77%);
            border-radius: 20px 0 0 20px;
        }

        &-wrap {
            margin-top: 30px;
            font-size: 16px;
            font-weight: 700;
            text-transform: uppercase;
            line-height: normal;
            display: flex;
            align-items: center;
            justify-content: space-between;
            color: #fff;
            span {
                
            }
        }

        .irs.irs--flat .irs-min,
        .irs.irs--flat .irs-max {
            display: none !important;
        }

        .irs.irs--flat .irs-single {
            font-family: Raleway;
            color: #0BE05C;
            font-weight: 700;
            font-size: 40px;
            line-height: normal;
            background-color: transparent;
            top: -40px;

            &::before {
                content: none;
            }
        }

        .irs.irs--flat .irs-handle {
            width: 40px;
            height: 40px;
            background-color: #0BE05C;
            border-radius: 50%;
            top: 11px;

            & > i:first-child {
                width: 24px;
                height: 24px;
                background-color: #fff;
                border-radius: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                margin-left: 0;
            }
        }
    }

    &__prices {
        max-width: 483px;

        &-title {
            font-size: 15px;
            font-weight: 700;
            line-height: 130%;
            text-transform: uppercase;
            color: #00D5C8;
            margin-bottom: 20px;
        }

        &-text {
            font-size: 12px;
            font-weight: 500;
            line-height: 120%;
            color: #fff;
            margin-top: 30px;
            a {
                color: currentColor;
                text-decoration: underline;
            }
        } 

        &-list {
            display: flex;
            align-items: center;
        
            button {
                font-size: 18px;
                font-weight: 700;
                line-height: normal;
                text-transform: uppercase;
                color: #fff;
                border-radius: 50px;
                border: 2px solid #00D5C8;
                padding: 7px;
                width: 100%;
                max-width: 110px;
                transition: background-color .3s linear;
                &.is-active {
                    background-color: #00D5C8;
                }
                &:hover:not(:active):not(.is-active) {
                    background-color: rgba(0, 213, 200, .5);
                }

                &:not(:last-child) {
                    margin-right: 12px;
                }
            }
        }
    }
}